import React, { useState } from 'react';
import PilihKota from './PilihKota';
import TampilJadwal from './TampilJadwal';

const JadwalShalat = () => {
    const [idKota, setIdKota] = useState(148)
    const gantiKota = e => {
        let kota = e.target.value
        setIdKota(kota)
    }
    return(
        <div className="pt-wrapper">
            <div className="container">
                <PilihKota idkota={idKota} onchange={gantiKota} />
                <TampilJadwal idkota={idKota} />
            </div>
        </div>
    )
}
export default JadwalShalat;