import React from 'react';
import kotas from '../libs/datakota'

const PilihKota = (props) => {
    const opsikota = kotas.map( (item, index) => (
        <option key={index} value={item.id}>{item.name}</option>
    ))
    return(
        <div className="pt-pilihkota">
            <select name="pilihkota" id="pilihkota" defaultValue={props.idkota} onChange={props.onchange} className="pilihkota">
                {opsikota}
            </select>
        </div>
    )
}
export default PilihKota;