import React, { Fragment } from 'react';
import prayTimes from '../libs/PrayTimes'
import kotas from  '../libs/datakota'

let date = new Date()
let month = date.getMonth()
let year = date.getFullYear()
prayTimes.tune( {imsak: 2, subuh: 2, dhuhur: 2, ashar: 2, magrib: 2, isya: 2} )

const TampilJadwal = props => {
    const displayKota = () => {
        let lists = []
        kotas.map( (item, index) => {
            // eslint-disable-next-line
            if(item.id == props.idkota) {
                lists.push(item.id, item.name, item.area)
            }
            return lists
        })
        return (
            <Fragment>
                <div className="tabel-heading">
                    <div className="innertext">
                        <span>Area: {lists[2]}</span>
                    </div>
                    <div className="innertext">
                        <span>Kab/Kota: {lists[1]}</span>
                    </div>
                </div>
                {getDataTabel()}
            </Fragment>
        )
    }
    const getDataTabel = () => {
        let items = {
            day: 'Tgl',
            imsak: 'Imsak',
            subuh: 'Subuh',
            dhuhur: 'Dhuhur',
            ashar: 'Ashar',
            magrib: 'Magrib',
            isya: 'Isya'
        }
        let tableHeads = makeRows(items, items, 'head-row')
        let strDate = new Date(year, month, 1)
        let endDate = new Date(year, month+1, 1)
        let tableRows = [];
        let i = 0;
        while(strDate < endDate) {
            let lists = []
            kotas.map(item => {
                // eslint-disable-next-line
                if(item.id == props.idkota) {
                    let garis = item.geo[0].split(",")
                    let x = garis[0]
                    let y = garis[1]
                    let elev = garis[2]
                    let tz = (item.tz).replace(/0/g, '')
                    lists.push(x, y, elev, tz)
                }
                return lists
            })
            let times = prayTimes.getTimes(strDate, [lists[0],lists[1],lists[2]], lists[3])
            times.day = strDate.getDate();
            let isToday = (strDate.getMonth() === date.getMonth()) && (strDate.getDate() === date.getDate())
            let kelas = isToday ? 'day today' : 'day'
            tableRows[i] = makeRows(times, items, kelas, i, true)
            i++;
            strDate.setDate(strDate.getDate() + 1)
        }
        return(
            <div className="tablewrapper">
                <div className="headings">{tableHeads}</div>
                <div className="alldays">{tableRows}</div>
            </div>
        )
    }
    const makeRows = (data, items, kelas, idx = 0, cls = false) => {
        let rows = Object.keys(items).map( (key, index) => {
            let itemtrue = (cls === true) ? `item itemwaktu itemwaktu-${index+1}` : `item`
            return(
                <span className={`${itemtrue}`} key={index}>{data[key]}</span>
            )
        })
        return(
            <div className={kelas} key={idx}>{rows}</div>
        )
    }
    return(
        <div className="tabel-bodi">{displayKota()}</div>
    )
}
export default TampilJadwal